import React from 'react'
import styled from 'styled-components'
import Cup from '../img/fullcup.jpeg'

const Container = styled.div`
display: flex;
@media only screen and (max-width: 480px) {
       flex-direction: column;
       padding: 20px
}
`
const Left = styled.div`
width: 50%;
@media only screen and (max-width: 480px) {
   display: none;
    }
`
const Image = styled.img`
width: 80%;
@media only screen and (max-width: 480px) {
   display: none;
}
`
const Right = styled.div`
width: 50%;
display: flex;
flex-direction: column;
justify-content: center;
margin-top: 30px;

@media only screen and (max-width: 480px) {
   width: 100%;
   flex-direction: column;
   align-items: center;
}
`

const Title = styled.span`
font-size: 70px;
@media only screen and (max-width: 480px) {
    font-size: 40px;
}
`
const SubTitle = styled.p`
font-size: 24px;
font-style: italic;
color: #333;
margin-top: 30px;
@media only screen and (max-width: 480px) {
    font-size: 20px;
    margin-top: 20px;
}
`
const Desc = styled.p`
font-size: 20px;
color: #777;
margin-top: 30px;
`

const Button = styled.button`
width: 150px;
border: none;
margin-top: 20px;
padding: 15px 20px;
background-color: darkblue;
color: white;
font-size: 20px;
border-radius: 20px;
cursor: pointer;
transition: all 200ms ease;
:hover{
    background-color: #2525b6;
}
`

const Feature = () => {
    return (
        <Container>
            <Left><Image src={Cup} /></Left>
            <Right>
                <Title>
                    Here at <b>Corp. Things</b> <br />
                    We offer you a <b>Full Cup</b>
                </Title>
                <SubTitle>A creative edge means your glass will never be half full.</SubTitle>
                <Desc>Our products will help you sharpen your edge through meaningful experiences and sleek designs.</Desc>
                <Desc>Maintaining that edge means a bottomless cup.</Desc>
                <Button>Learn More</Button>
            </Right>

        </Container >
    )
}

export default Feature
