import React from 'react'
import styled from 'styled-components'
import Map from '../img/map.png'
import Phone from '../img/phone.png'
import Send from '../img/send.png'

const Container = styled.div`
height: 90%;
`

const Wrapper = styled.div`
height: 100%;
padding: 20px;
display: flex;
justify-content: center;
align-items: center;
@media only screen and (max-width: 480px){
   flex-direction: column;
}
`
const FormContainer = styled.div`
width: 50%;
@media only screen and (max-width: 480px){
   width: 100%;
}
`

const Title = styled.h1`
text-align: center;
margin-bottom: 1em;
@media only screen and (max-width: 480px){
  margin: 20px;
  font-size: 30px;
}
`

const Form = styled.form`
height: 250px;
display: flex;
align-items: center;
justify-content: center;
@media only screen and (max-width: 480px){
   flex-direction: column;
}
`
const FormLeft = styled.div`
height: 100%;
display: flex;
flex-direction: column;
justify-content: space-between;
margin-right: 20px;
@media only screen and (max-width: 480px){
   height: 50%;
   margin-right: 0;
}
`

const FormRight = styled.div`
height: 100%;
display: flex;
flex-direction: column;
justify-content: space-between;
@media only screen and (max-width: 480px){
   height: 50%;
  
}
`
const Input = styled.input`
width: 200px;
padding: 20px;
border-radius: 10px;
border: 1px solid grey;
@media only screen and (max-width: 480px){
  padding: 10px;
}
`

const TextArea = styled.textarea`
width: 200px;
height: 60%;
padding: 20px;
border-radius: 10px;
border: 1px solid grey;
@media only screen and (max-width: 480px){
  padding: 10px;
  margin-top: 10px;
}
`
const Button = styled.button`
margin-top: 20px;
border: none;
padding: 17px;
background-color: darkblue;
color: white;
font-size: 20px;
border-radius: 10px;
cursor: pointer;
transition: all 200ms ease;
:hover{
    background-color: #2525b6;
}
@media only screen and (max-width: 480px){
  padding: 10px;
  font-size: 15px;
  margin-top: 15px;
}
`

const AddressContainer = styled.div`
width: 50%;
display: flex;
flex-direction: column;
align-items: center;
@media only screen and (max-width: 480px){
 width: 100%;
 margin-top: 20px;
 margin-bottom: 20px;
}
`
const Address = styled.div`
display: flex;
align-items: center;
margin-bottom: 50px;
@media only screen and (max-width: 480px){
  margin-bottom: 10px;
  flex-direction:column;
}
`;

const Icon = styled.img`
width: 20px;
margin-right: 20px;
@media only screen and (max-width: 480px){
 width: 15px;
}
`

const Text = styled.span`
font-size: 20px;
margin-right: 20px;
@media only screen and (max-width: 480px){
 font-size: 14px;
 margin-right: 10px;
}
`

const Contact = () => {
    return (
        <Container>
            <Wrapper>
                <FormContainer>
                    <Title>Any Comments or Questions? </Title>
                    <Form>
                        <FormLeft>
                            <Input placeholder="Your Name" />
                            <Input placeholder="Your Email" />
                            <Input placeholder="Title" />
                        </FormLeft>
                        <FormRight>
                            <TextArea placeholder="Your Message" />
                            <Button>Send</Button>
                        </FormRight>
                    </Form>
                </FormContainer>
                <AddressContainer>
                    <Address>
                        <Icon src={Map} />
                        <Text>321 Fake Address, State, USA</Text>
                    </Address>
                    <Address>
                        <Icon src={Phone} />
                        <Text>+1 234-5678</Text>
                        <Text>+1 987-6543</Text>
                    </Address>
                    <Address>
                        <Icon src={Send} />
                        <Text>contact@companyemail.com</Text>
                        <Text>email@companyemail.com</Text>
                    </Address>
                </AddressContainer>
            </Wrapper>
        </Container>
    )
}

export default Contact
