import React from 'react'
import styled from 'styled-components'
import Knife from '../img/knife.jpeg'
import AnimatedShape from './AnimatedShape';

const Container = styled.div`
height: calc(100vh - 50px);
display: flex;
padding: 20px;

@media only screen and (max-width: 480px) {
 flex-direction: column;
}
`;
const Left = styled.div`
width: 60%;
display: flex;
flex-direction: column;
align-items: center;
justify-content: center;

@media only screen and (max-width: 480px) {
    width: 100%;
    height: 100%;
}
`;
const Title = styled.h1`
width: 60%;
font-size: 60px;
@media only screen and (max-width: 480px) {
    width: 100%;   
    font-size: 50px; 
    color: #838080;
}
`;

const Desc = styled.p`
width: 60%;
font-size: 20px;
margin-top: 20px;
@media only screen and (max-width: 480px) {
    width: 100%;  
    color: #838080;
}
`;

const Info = styled.div`
margin-top: 50px;
width: 60%;
display: flex;
justify-content: space-between;
align-items: center;
@media only screen and (max-width: 480px) {
    width: 100%;  
    flex-direction: column;
}
`;

const Button = styled.button`
padding: 15px;
background-color: darkblue;
color: white;
border-radius: 10px;
font-weight: bold;
border: none;
letter-spacing: 1.5px;
cursor: pointer;
transition: all 200ms ease;

:hover{
    background-color: #2525b6;
}
@media only screen and (max-width: 480px) {
    margin-bottom: 20px;
}
`;
const Contact = styled.div`
display: flex;
flex-direction: column;
`;

const Phone = styled.span`
color: #fd8398;
font-weight: bold;
`
const ContactText = styled.span`
color: grey;
margin-top: 5px;
`

const Right = styled.div`
width: 40%;

@media only screen and (max-width: 480px) {
 display: none;
}
`;

const Image = styled.img`
width: 100%;

`

const Intro = () => {
    return (
        <Container>
            <Left>
                <Title>Sharpen Your Creative Edge</Title>
                <Desc>Your products and ideas are your tools. We will provide the whetstone.</Desc>
                <Info>
                    <Button>START A PROJECT</Button>
                    <Contact>
                        <Phone>Call us at (01) 234-5678 </Phone>
                        <ContactText>For anything you would like to ask us!</ContactText>
                    </Contact>
                </Info>
            </Left>
            <Right><Image src={Knife}></Image></Right>
            <AnimatedShape />
        </Container>
    )
}

export default Intro
