import React from 'react'
import styled from 'styled-components'


const Container = styled.div`
margin-right: 50px;
padding: 20px 80px;
background-color: white;
-webkit-box-shadow: 0px 0px 18px -11px rgba(0,0,0,0.5); 
box-shadow: 0px -0px 0px 18px -11px rgba(0,0,0,0.5);
border-radius: 10px;
display: flex;
flex-direction: column;
align-items: center;
@media only screen and (max-width: 480px){
  margin-right: 0;
  margin-bottom: 5px;
  padding: 10px 20px;
  }
`

const PriceContainer = styled.div`
display: flex;
align-items: center;
`
const Price = styled.span`
font-weight: bold;
font-size: 50px;
@media only screen and (max-width: 480px){
   font-size: 30px;
}
`
const Type = styled.button`
padding: 10px;
margin: 10px 0;
border: 1.5px solid crimson;
border-radius: 20px;
color: crimson;
background-color: white;
@media only screen and (max-width: 480px){
   display: none;
}
`

const List = styled.ul`
list-style: none;
`
const ListItem = styled.li`
margin: 30px 0;
@media only screen and (max-width: 480px){
   margin: 10px;
   font-size: 14px;
}
`

const Button = styled.button`
padding: 15px;
border-radius: 10px;
border: none;
background-color: darkblue;
color: white;
font-size: 16px;
font-weight: bold;
cursor: pointer;
transition: all 200ms ease;
:hover{
    background-color: #2525b6;
}
@media only screen and (max-width: 480px){
   padding: 5px;
}
`

const PriceCard = ({ price, type }) => {
    return (
        <Container>
            <PriceContainer>
                $<Price>{price}</Price>/month
            </PriceContainer>
            <Type>{type} Plan</Type>
            <List>
                <ListItem>Technical Support</ListItem>
                <ListItem>Custom Plugins</ListItem>
                <ListItem>Unlimited Bandwidth</ListItem>
                <ListItem>Item 4</ListItem>
            </List>
            <Button>Start Now</Button>
        </Container>
    )
}

export default PriceCard
