import React from 'react'
import styled from 'styled-components'

const Square = styled.div`
width: 60px;
height: 60px;
background-color: darkblue;
opacity: 0.7;
position: absolute;
top: -60px;
left: -60px;
z-index: -1;
animation: square 22s linear alternate infinite;

    @keyframes square{
        to{
            transform: translate(100vw, 100vh);
        }
    }
    `;

const Circle = styled.div`
width: 100px;
height: 100px;
border-radius: 50%;
background-color:  #fd8398;
opacity: 0.8;
position: absolute;
top: 800px;
left: -90px;
z-index: -1;
animation: circle 30s linear alternate infinite;

    @keyframes circle{
        to{
            transform: translate(100vw, -20vh);
        }
    }
    `;

const Rect = styled.div`
width: 50px;
height: 100px;
background-color: green;
opacity: 0.5;
position: absolute;
top: 400px;
left: -60px;
z-index: -1;
animation: rect 25s linear alternate infinite;

    @keyframes rect{
        to{
            transform: translate(100vw, -50vh);
        }
    }
`;

const AnimatedShape = () => {
    return (
        <>
            <Square />
            <Circle />
            <Rect />
        </>
    )
}

export default AnimatedShape
