import React, { useState } from 'react'
import styled from 'styled-components'
import Business from '../img/business-woman.png'
import MiniCard from './MiniCard'
import Play from '../img/play.png'
import Teamwork from '../img/teamworkvideo.mp4'

const Container = styled.div`
display: flex;
height: 100%;

@media only screen and (max-width: 480px){
    flex-direction: column;
}
`
const Left = styled.div`
width: 50%;
position: relative;
@media only screen and (max-width: 480px){
   display: none;
}
`

const Image = styled.img`
display: ${(props) => props.open && "none"};
height: 100%;
margin-left: 100px;
position: absolute;
bottom: 0;
`

const Video = styled.video`
    display: ${(props) => !props.open && "none"};
    height: 300px;
    position: absolute;
    top: 0;
    bottom: 200px;
    right: 200px;
    margin: auto;
    @media only screen and (max-width: 480px){
    width: 100%;
    bottom: 0;
    right: 0;
}
`

const Right = styled.div`
width: 50%;
@media only screen and (max-width: 480px){
    width: 100%;
}
`

const Wrapper = styled.div`
padding: 50px;
display: flex;
flex-direction: column;
@media only screen and (max-width: 480px){
    padding: 20px;  
    align-items: center;
}
`

const Title = styled.h1`

`
const Desc = styled.p`
font-size: 20px;
margin-top: 20px;
color: #555;
`

const CardContainer = styled.div`
display: flex;
justify-content: space-between;
margin-top: 50px;

`
const Button = styled.button`
width: 180px;
border: none;
border-radius: 10px;
background-color: darkblue;
color: white;
font-size: 20px;
padding: 15px;
margin-top: 50px;
cursor: pointer;
display: flex;
align-items: center;
transition: all 200ms ease;
:hover{
    background-color: #2525b6;
}
`

const Icon = styled.img`
width: 20px;
margin-right: 10px;
`

const Modal = styled.div`
width: 100vw;
height: 100vh;
position: absolute;
top: 0;
left: 0;
background-color: #00000050;
`
const Close = styled.button`
position: absolute;
right: 5px;
top:180px;
background-color: white;
padding: 10px;
border: none;
border-radius: 5px;
`

const Service = () => {
    const [isOpen, setOpen] = useState(false)
    const smallScreen = window.screen.width <= 480 ? true : false

    return (
        <Container>
            <Left>
                <Image open={isOpen} src={Business} />
                <Video open={isOpen} autoPlay loop controls src={Teamwork} />
            </Left>
            <Right>
                <Wrapper>
                    <Title>Start Today</Title>
                    <Desc>Starting with Corp. Things and gaining an edge in
                        your market is simple if you let us do the heavy lifting for you.</Desc>
                    <CardContainer>
                        <MiniCard></MiniCard>
                        <MiniCard></MiniCard>
                        <MiniCard></MiniCard>
                    </CardContainer>
                    <Button onClick={() => setOpen(true)}>
                        <Icon src={Play} />Start Here
                    </Button>
                </Wrapper>

            </Right>
            {smallScreen && isOpen && (
                <Modal>
                    <Video open={isOpen} autoPlay loop controls src={Teamwork} />
                    <Close onClick={() => { setOpen(false) }}>Close</Close>
                </Modal>
            )}
        </Container >
    )
}

export default Service
