import React from 'react'
import styled from 'styled-components'

const Container = styled.div`
height: 50px;
`;
const Wrapper = styled.div`
padding: 10px 20px;
display:flex;
align-items: center;
justify-content: space-between;
`;

const Left = styled.div`
width:60%;
display: flex;
align-items: center;
justify-content:space-between;
`;

const Logo = styled.h1`
font-weight: bold;
text-decoration: underline crimson;
`;

const Menu = styled.ul`
display: flex;
list-style: none;

@media only screen and (max-width: 480px) {
 display: none;
}
`;
const MenuItem = styled.li`
margin-right:30px;
font-size:20px;
font-weight:bold;
color: grey;

`;

const MenuLink = styled.a`
text-decoration: none;
color: grey;
transition: all 200ms ease;
:active{
    color: grey;
}
:hover{
 color: black;
}
`

const Button = styled.button`
    border: 2px solid white;
    padding: 10px 15px;
    background-color: black;
    color: white;
    font-weight: bold;
    border-radius: 10px;
    cursor: pointer;
    transition: all 200ms ease;
    :hover{
        color: black;
        background-color: white;
    }
`

const Navbar = () => {
    return (
        <Container>
            <Wrapper>
                <Left>
                    <Logo>Corp. Things</Logo>

                    <Menu>
                        <MenuItem>
                            <MenuLink href="#Intro">Home</MenuLink>
                        </MenuItem>
                        <MenuItem>
                            <MenuLink href="#Feature">Features</MenuLink>
                        </MenuItem>
                        <MenuItem>
                            <MenuLink href="#Service"> Services</MenuLink>
                        </MenuItem>
                        <MenuItem>
                            <MenuLink href="#Price">Pricing</MenuLink>
                        </MenuItem>
                        <MenuItem>
                            <MenuLink href="#Contact">Contact</MenuLink>
                        </MenuItem>
                    </Menu>
                </Left>

                <Button>JOIN TODAY</Button>
            </Wrapper>

        </Container >
    )
}

export default Navbar
